@import "/src/media-query-mixins";
@import "/src/dimentions";

.cover {
  z-index: 1;
  width: 100vw;
  height: 100%;
  display: flex;
  flex-direction: column;

  .header {
    position: absolute;
    top: 20%;
    left: $gutter-phone;
    width: 66%;
    font-size: 27px;
    line-height: 31px;
    text-transform: uppercase;
    font-weight: lighter;
    z-index: 1;

    @include media('>phone') {
      font-size: 50px;
      line-height: 58px;
      left: $gutter-tablet;
      width: 60%;
    }

    @include media('>tablet') {
      font-size: 65px;
      left: $gutter-desktop;
    }

    @include media('>desktop') {
      font-size: 91px;
      line-height: 81px;
      left: $gutter-wide-screen;
      width: 55%;
    }
  }

  .art {
    height: 50%;
    bottom: 0;
    right: 0;
    position: absolute;

    @include media('>tablet') {
      height: 70%;
      bottom: 8%;
    }

  }

  .footer {
    height: 15%;
    width: 100%;
    position: absolute;
    bottom: 0;
    z-index: 2;
    display: flex;
    flex-direction: column;
    padding-left: $gutter-phone;
    justify-content: center;
    box-sizing: border-box;

    @include media('>phone') {
      height: 127px;
      padding-left: $gutter-tablet;
    }

    @include media('>tablet') {
      padding-left: 100px;
      left: 35%;
      right: 10%;
      width: auto;
    }

    @include media('>desktop') {
      font-size: 91px;
    }
  }

  .text {
    width: 220px;
    text-align: left;
    line-height: 1.3em;
    font-size: 16px;
    transform: translateY(1px);
  }

  .title {
    color: white;
    width: 100%;
  }

  .date {
    width: 100%;
  }

  .arrow {
    position: absolute;
    top: 50%;
    right: $gutter-phone;
    color: white;
    height: 46px;

    @include media('>phone') {
      right: $gutter-tablet;
    }

    @include media('>tablet') {
      right: 100px;
    }
  }
}
